import { Component } from 'react'
import marked from 'marked'
import hljs from "highlight.js"
import 'highlight.js/styles/github.css';

import './myMarkdown.css'

export default class MyMarkdown extends Component {
  constructor(props) {
    super(props)

    marked.setOptions({
      renderer: new marked.Renderer(),
      highlight: function (code) {
        return hljs.highlightAuto(code).value;
      },
      gfm: true, //  Git Hubmarkdown.
      pedantic: false, // false
      sanitize: false, // html
      tables: true, //  gfm true
      breaks: true, //  gfm true
      smartLists: true, // markdown
      smartypants: false, // 
    })
  }

  render(){
    return(
      <div className="show-html" 
      dangerouslySetInnerHTML={{ __html: marked(this.props.content ? this.props.content : '')}}/>
    )
  }
}